@font-face {
  font-weight: 300;
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-weight: 900;
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

body {
  margin-top: 0;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

.search {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  align-items: stretch;
  margin-bottom: 20px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search--big {
  margin-top: 20px;
}

.search--big:hover {
  box-shadow: 0 4px 4px #00000040;
}

.search--smole:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search--big:focus-within {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  box-shadow: 0 4px 4px #00000040;
}

.search--smole:focus-within {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
}

.search__bar {
  color: #3d4e61;
  appearance: none;
  background: inherit;
  border: none;
  width: 100%;
  font-family: inherit;
}

.search__bar::-webkit-search-cancel-button {
  display: none;
}

.search__bar:focus {
  appearance: none;
  outline: none;
}

.search__bar--big {
  padding: 23px 0;
  font-size: 16px;
}

.search__bar--big:focus {
  text-shadow: 0 4px 4px #00000040;
}

.search__bar--smole {
  padding: 10px 0;
  font-size: 14px;
}

.search__bar--smole:focus {
  font-weight: 900;
}

.search__icon {
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.search__labl {
  align-items: center;
  display: flex;
}

.search__labl--big {
  padding: 0 17px 0 26px;
}

.search__labl--smole {
  padding: 0 10px 1px 12px;
}

.search__icon--big {
  width: 19px;
  height: 19px;
}

.search__icon--smole {
  width: 11px;
  height: 11px;
}

